@use "./pre-register.scss";
@use  "./main_page.scss";
@use "./font-awesome.scss" ;
@import url('https://fonts.googleapis.com/css2?family=Amiri+Quran&family=Permanent+Marker&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Almarai&family=Fuzzy+Bubbles:wght@400;700&display=swap');

:root{
  --primary:#8C16E6;
  --primary-dark:#6511A6;
  --secondary:#e49102;
  --secondary-light:#D1E617;

  --gold:#FFDF00;
  --dark-gold:#f4d400;

  --green:rgb(14, 200, 14);
  --dark-green:rgb(12, 156, 12);
}
body{
  font-family: 'Almarai', sans-serif;
}
.no-ff{font-family: sans-serif,serif;}
.f-basis-100{flex-basis: 100%;}
.epic-text{
  font-family: 'Permanent Marker' ,'Amiri Quran' , serif;
}
.underline{
  text-decoration: underline;
}

.bg-primary{
  background-color: var(--primary) !important;
}
.bg-primary-bs{
  background-color: #0d6efd ;
}
.bg-primary-dark{
  background-color: var(--primary-dark) !important;
}

.secondary{
  color: var(--secondary) !important;
  border:3px solid var(--secondary) !important;
  background-color: #f6ffa3 !important;
}
.text-secondary{
  color: var(--secondary) !important;
}
.border-secondary{
  border:2px solid var(--secondary) !important;
}
.bg-secondary-light{
  background-color: #D1E617;
}



.flex-gap{
  gap: 10px;
}

.size-sm{
  transform-origin: center;
  transform: scale(.7);
}


@keyframes load {
  0%{
    transform: scale(0);
  }
  80%{
    transform: scale(1.2);
  }
  100%{
    transform: scale(1);
  }
}