.badge{
  border-radius: 50%;
}
.size-sm{
  transform-origin: center;
  transform: scale(.7);
}
    .tooltip.show{
      opacity: 0.97 !important;
    }

.register-form{
  min-height: 100vh;
}

  .TaskInput{
    box-sizing: unset;margin: auto;
    display: flex;

    align-items: center;
    max-width: 730px;
    max-height: 50px;
    padding: 10px;
    gap: 5px;

    opacity: 0;
    transition: opacity 250ms ease-in-out ;
    &.showing{
      opacity: 1;
    }


    & > * {
      flex-basis: 0;
    }
    button{
      flex-grow:2;
      max-width: 87px;
    }

    .select{
      position: relative !important;
      flex-grow:3;
      height: 100%;

      & > select{
        width: 100%;color: white;
      }

    }

    input{
      flex-grow:16;
      padding: 5px 5px;
    }
  }


  .TaskInputSelect{
    &::before{
      content:"";
      position: absolute;
      inset: 0;
      padding: .25em 0; text-align: center;  overflow: hidden;
      border-radius: 0.375rem;
      border: 2px solid black;
      pointer-events: none;
      z-index: 10;
    }
    &.wajib::before{
      content:"واجب";
      background-color: #198754;
    }
    &.mousta7ab::before{
      content:"مستحب";
      background-color: #ffffff;
      color: rgb(2, 81, 2);

    }
    &.mamnou3::before{
      content:"ممنوع";

      background-color: #bb2d3b;
    }
  }

  .AddTask{
    max-width: 730px;
  }
  #register ,#addDayBtn{
    color: white;
    background: linear-gradient(-16deg, #90F9C4 0%, #0fb080 50% );
    width: 100%;
    flex-grow: 10;
    flex-basis: auto;
    margin: 0 10px;



    &:hover{
      background: linear-gradient(-16deg, #77cfa3 0%, #0b7b59 80% );

    }
  }

@media (min-width:720px) {
  #inputNameGroup{
    width: 75% !important;
    max-width: 600px;
  }
}