#nav , .footer{
  position: relative;
  gap: 10px;
  font-family: 'Permanent Marker' ,'Amiri Quran' , serif;
}

  #gc-pfp{
    min-width: 80px;
    min-height: 80px;
    aspect-ratio: 1/1;
    background-color: rgb(255, 255, 255);

    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 50%;
    border: 1px solid white;

    &.gc1{
      background-image: url("../assests/gc1.jpg");
    }
    &.gc2{
      background-image: url("../assests/gc2.png");
    }
    &.gc3{
      background-image: url("../assests/gc3.jpg");
    }
    &.gc4{
      background-image: url("../assests/gc4.jpg");
    }
  }

  .daysLeft{
    position: fixed;top: 0px;left: 0px;
    background-color: black;
    border: 4px solid white;
    padding: .125em .8em;
    z-index: 100;
  }
#main{
  min-height: 500px;

  background: rgb(101,17,166);
  background: linear-gradient(120deg, rgba(101,17,166,1) 0%, rgba(140,22,230,1) 100%);
}


#addDayBtn{
  display: block;
  max-width: 420px;
  margin: auto;
}

#addDayModal{
 .all{
  color: white;
  background-color: var(--primary);
 }


}
.score{
font-family: sans-serif;
}
    .score-bar{
      --basic:0;
      --bonus:0;
      --duration:.7s;

      position: relative;
      width: 60%;max-width: 220px;min-width: 134px;
      height: 2em;max-height: 50px;
      
      border: 2px solid black;
      border-radius: 0  .375rem .375rem 0;
      overflow: hidden;

      & .score-bar-basic , & .score-bar-bonus{
        height: 100%;
        transition: all var(--duration) ease-out;

      }
      & .score-bar-basic{
        width: var(--basic);
        background-color: var(--green);


      }
      & .score-bar-bonus{
        position: absolute; top: 0;left: 0;
        background-color: var(--gold);
        width: var(--bonus);
      }
    }
    .score-percent{
      transition: color 450ms ease-in-out;
      &.white{color: white !important;}
      &.basic-score-percent{
        color: var(--green);
      }
      &.bonus-score-percent{
        color: var(--gold);
      }
    }


.last-time{
  margin: auto;
  color: white;
  background-color: rgba(0, 0, 0, 0.288);
  padding: .4em 1em;
  text-align: right;
  width: 100%; max-width: 420px
  ;
}
.static-wajib-tasks,.static-mamnou3-tasks,.static-mousta7ab-tasks{
  text-align: right;

  & input{
    aspect-ratio: 1/1;
    width: 20px;height: 20px;
  }

}
.static-wajib-tasks{background-color: rgb(184, 247, 184) ;}
.static-mamnou3-tasks{
  background-color: rgb(255, 157, 157);
}

.static-mousta7ab-tasks{background-color: #fff3a6;}

.array-scroller{
  display: flex;justify-content: center;
  flex-wrap: wrap;
  border-radius: 20px 20px 0 0;

  & button{

    height: min-content;padding: .2em 1em;
    border-left: 2px solid white;
    border-radius: 0 !important;
    
    &:hover{border-left: 2px solid white;
    opacity: .8;}
    &:nth-last-of-type(1){border-right: 2px solid white;}
  }
}

.all-days-grid{
  --color:red;
  position: relative;
  display: grid;
  grid-template-columns: repeat( auto-fill, minmax(160px, 250px) );
  justify-content: center;

  border-radius:  0 0 20px 20px;

  // gap: .2em;

}
.last-day.card{
  animation: load 300ms ease-in-out;
}
.single-day{
  --border-width:4px;
  position: relative;
  border: var(--border-width) solid white;
  border-right-width: calc( var(--border-width)/2) ;
  border-left-width:calc(var(--border-width)/2) ;

  background: rgb(0,0,0);
  background: linear-gradient(175deg, rgba(0,0,0,1) 0%, rgba(157,157,157,1) 65%);
  padding: .5em;
  border-radius: 1em;

  cursor: pointer;
  &:hover{
    opacity: .85;
  }
}
    .date-and-id{
      display: flex;
      justify-content: space-between;
      color: white;

      & .day-id{
        background-color: white;
        padding: .125em .5em;
        color: black;
        border-radius: 0 0 10px 10px;
        width: 1.7em;
        text-align: center;
      }

    }

    .score-bar{
      margin: 10px auto;
      border-width: 2px;
      // border-color: white;
    }


    .auto-selector{
      & input:checked{
        background-color: gray;
      }
    }

.daily-task:first-of-type{
  border-bottom: solid 2px black;
  border-bottom: none;
  border-radius: 10px 10px 0 0;
}
  
.daily-task{
  background-color: rgb(51, 48, 48);
  border-top: solid 2px black;
  padding-top: .35em ;
  padding-bottom: .35em ;
  padding-right: .5em;

  & input:checked{
    background-color: var(--color);

  }
  cursor: pointer;
  & *{
    cursor: pointer;
  }
}
.wajib-task ,.mamnou3-task{
  --color:var(--green);
}

.mousta7ab-task{
  --color:var(--dark-gold);
}



.wajib-task:first-of-type {
    border-width: 15px;
    border-top-color: var(--green);
  }


@media (max-width:410px) {
.all-days-grid{
  gap: .5em;
}
  .single-day{
    border-radius: .735em;
    border-right-width: unset ;
    border-left-width:unset;
  }
}


